form{
    width: 90%;
    max-width: 300px;
    height: 50%;
    margin-top: calc(30% - 25px);
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 25px;
}

label{
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
    font-family: 'Roboto', sans-serif;
}

textarea{
    background-color: #F9FBFC;
    border: 2px;
    border-radius: 25px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
    font-family: 'Roboto', sans-serif;
}

textarea::placeholder {
    color: #A5A5A5;
    font-weight: 400;
}

textarea:focus{
    outline-color: #2518d6;
}

.TodoForm-buttonContainer{
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoForm-button{
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Roboto', sans-serif;
}

.TodoForm-button--add{
    background: #1920e7;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, .5);
    color: white;
    border-radius: 25px;
}

.TodoForm-button--cancel{
    background: transparent;
}

